.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.card{
  display: inline-block;
  text-align: center;
  width: 3em;
  font-size: 2em;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: 1em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.activeCard{
  color:red;
  background: pink;
}
.member-photo{
  display: block;
  margin-left: auto;
  margin-right: auto;
  height:300px;
  width:250px;
  margin-top:1em;
  margin-bottom: 1em;
}
.acenter{
  text-align: center;
}
.dis-none{
  display: none;
}
.center-button{
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.kanit{
  font-family: 'Kanit', sans-serif;
}
.hotpink{
  color:hotpink;
}
.kanitlight{
  font-family: 'Kanit', sans-serif;
  font-weight: 300;
}


